

<!--搜索页面-->


<template>
    <div>
        <!--头部-->
        <header id="head_top">
            <section class="head_goback" @click="goBack">
                <i class="el-icon-arrow-left"></i>
            </section>
            <section class="title_head ellipsis">
                <span class="title_text">
                    搜索
                </span>
            </section>
        </header>
        <!--输入框-->
        <div class="search_form">
            <input type="search" placeholder="请输入商家或美食名称" class="search_input" v-model="value">
            <input type="submit" class="search_submit" @click="submit">
        </div>
        <!--历史记录-->
        <section class="search_history" v-if="historyShow">
            <h4 class="title_restaurant" v-if="">搜索历史</h4>
            <ul>
                <li class="history_list" v-for="(name,index) in history"  >
                    <span class="history_text" ref="name" @click="historyReq(name)">{{name}}</span>
                    <i class="el-icon-close" @click="deleteLi(index)"></i>
                </li>
            </ul>
            <footer class="clear_history" @click="deleteHistory">清空历史记录</footer>
        </section>
        <!--搜索到的店铺信息-->
        <section v-if="!isShow">
            <h4 class="title_restaurant">商家</h4>
            <ul class="list_container">
                <li class="list_li" v-for="shop in restaurants" :key="shop.name">
                    <section class="item_left">
                        <img :src="'http://elm.cangdu.org/img/'+ shop.image_path" alt="" class="restaurant_img">
                    </section>
                    <section class="item_right">
                       <div class="item_right_text">
                           <p>
                               <span>{{shop.name}}</span>
                               <i class="el-icon-bank-card"></i>
                           </p>
                            <p>月售 {{shop.recent_order_num}}单</p>
                           <p>{{shop.float_minimum_order_amount}}起送 / 距离{{shop.distance}}</p>
                       </div>
                        <ul class="item_right_detail">

                        </ul>
                    </section>
                </li>
            </ul>
        </section>
        <!--搜索不到时的文字-->
        <div class="search_none" v-if="noneIsShow">
            很抱歉！无搜索结果
        </div>
        <!--底部-->
        <section id="foot_guide">
            <section class="guide_item" @click="take_out_food">
                <i class="el-icon-eleme"></i>
                <span>外卖</span>
            </section>
            <section class="guide_item" >
                <i class="el-icon-search"></i>
                <span>搜索</span>
            </section>
            <section class="guide_item" @click="goOrder">
                <i class="el-icon-tickets"></i>
                <span>订单</span>
            </section>
            <section class="guide_item" @click="goMy">
                <i class="el-icon-user"></i>
                <span>我的</span>
            </section>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Search",
        data(){
            return{
                isShow:true,
                value:"",
                end:[],
                restaurants:[],
                geohash:"",
                history:[],
                historyShow:true,
                historyValue:"",
                noneIsShow:false,
             }
        },
        created(){
            //进入页面获取历史信息
            this.placeHistory = JSON.parse(localStorage.getItem('placeHistory'))
            this.end = this.placeHistory.slice(-1)
            // console.log(this.end);
            //进入页面若没有历史记录，将历史文字隐藏
            this.history = JSON.parse(localStorage.getItem('searchHistory'));
           if (this.history == null){
               this.historyShow = false
           }
        },
        methods:{
            //头部返回上一级
            goBack(){
                this.$router.back(-1);
            },
            //点击按钮发起网络请求，获取店铺信息
            submit(){
                console.log(this.value)
                //关键字不为空才可以发起网络请求
                if (this.value != ""){
                    this.historyShow = false
                    this.isShow = false;
                    let a;
                    if (localStorage.getItem('searchHistory')== null || localStorage.getItem('searchHistory')== [] ){
                        a=[];
                        a.push(this.value)
                        localStorage.setItem('searchHistory',JSON.stringify(a));
                    } else {
                        a = JSON.parse(localStorage.getItem('searchHistory'));
                        console.log(a);
                        //去重逻辑
                        a.push(this.value)
                        let b =[]
                        b = a.filter(pro=>{
                            return pro != this.value;
                        });
                        b.push(this.value)
                        localStorage.setItem('searchHistory',JSON.stringify(b));

                    };



                    //获取关键字搜索信息
                    if (this.placeHistory == null) {
                        this.address ={
                            name: "上海迪士尼乐园",
                            address: "上海市浦东新区申迪西路753号",
                            latitude: 31.14419,
                            longitude: 121.66034,
                            geohash: "31.14419,121.66034"
                        }
                        this.geohash = this.address.geohash;
                    }else {
                        this.geohash = this.end[0].geohash
                    }
                    this.axios.get("v4/restaurants"+'?geohash='+this.geohash+'&keyword='+this.value,{
                    }).then(res=>{
                        // console.log(res);
                        this.errMsg(res)
                        this.restaurants = res;
                    }).catch(err=>{})

                }else {
                    this.isShow =true;
                }

            },
            //删除搜索历史
            deleteLi(index){
                this.history = JSON.parse(localStorage.getItem('searchHistory'));
                if (this.history.length == 1){
                    this.history.splice(index,1);
                    localStorage.removeItem('searchHistory');
                    this.historyShow=false
                } else {
                    this.history.splice(index,1);
                    localStorage.setItem('searchHistory',JSON.stringify(this.history))
                }

            },
            //清空搜索历史
            deleteHistory(){
                localStorage.removeItem('searchHistory');
                this.historyShow = false
            },
            //点击历史记录，发起网络请求，获取店铺信息
            historyReq(key){
                console.log(key)
                //获取关键字搜索信息
                if (this.placeHistory == null) {
                    this.address ={
                        name: "上海迪士尼乐园",
                        address: "上海市浦东新区申迪西路753号",
                        latitude: 31.14419,
                        longitude: 121.66034,
                        geohash: "31.14419,121.66034"
                    }
                    this.geohash = this.address.geohash;
                }else {
                    this.geohash = this.end[0].geohash
                }
                this.axios.get("v4/restaurants"+'?geohash='+this.geohash+'&keyword='+key,{
                }).then(res=>{
                    this.value = key
                    this.restaurants = res;
                    this.historyShow = false;
                    this.isShow = false
                    this.errMsg(res)
                }).catch(err=>{})
            },
            //如果未搜索到关键字，显示未有搜索结果
            errMsg(res){
                if (res == ""){
                    this.isShow = true;
                    this.historyShow=false;
                    this.noneIsShow = true
                }
            },
            take_out_food(){
                this.$router.push({
                    path:'/msite'
                })
            },
            goOrder(){
                this.$router.push({
                    path:'/order'
                })
            },
            goMy(){
                this.$router.push({
                    path:'/profile'
                })
            }
        },
    }
</script>

<style scoped>
    #head_top{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    .head_goback{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        line-height: 2.2rem;
        margin-left: .4rem;
        color: #fff;
    }
    .title_head{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    .title_text {
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    .search_form{
        background-color: #fff;
        padding: .5rem;
        display: flex;
        margin-top: 1.95rem;
    }
    .search_form .search_input{
        flex: 4;
        border: .025rem solid #e4e4e4;
        font-size: .65rem;
        color: #333;
        border-radius: .125rem;
        background-color: #f2f2f2;
        font-weight: 700;
        padding: 0 .25rem;
    }
    .search_form input{
        height: 1.5rem;
    }
    .search_form .search_submit{
        flex: 1;
        border: .025rem solid #3190e8;
        margin-left: .2rem;
        font-size: .65rem;
        color: #fff;
        border-radius: .125rem;
        background-color: #3190e8;
        font-weight: 700;
        padding: 0 .25rem;
    }
    .title_restaurant{
        font-size: .6rem;
        line-height: 2rem;
        text-indent: .5rem;
        font-weight: 700;
        color: #666;
    }
    .search_history .history_list{
        background-color: #fff;
        border-bottom: .025rem solid #e4e4e4;
        font: .7rem/2rem Microsoft YaHei;
        padding: 0 .3rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .search_history .history_list .history_text{
        display: inline-block;
        width: 80%;
    }
    .search_history .history_list i{
       font-size: 1rem;
    }
    .search_history .clear_history{
        background-color: #fff;
        color: #3190e8;
        font: .7rem/2rem Microsoft YaHei;
        font-weight: 700;
        text-align: center;
    }
    .list_container{
        background-color: #fff;
    }
    .list_li{
        display: flex;
        justify-content: center;
        padding: .5rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .list_li .item_left {
        margin-right: .25rem;
    }
    .list_li .item_right{
        font-size: .55rem;
        flex: 1;
    }
    .list_li .item_left .restaurant_img{
        width: 1.7rem;
        height: 1.7rem;
    }
    .item_right .item_right_text{
        padding-bottom: .25rem;
        border-bottom: .025rem solid #e4e4e4;
    }
    .item_right_text i{
        margin-bottom: -.08rem;
        font-size: .9rem;
    }
    .item_right_text p{
        line-height: .9rem;
    }
    .item_right_detail {
        margin-top: .25rem;
    }
    .search_none{
        margin: 0 auto;
        font: .65rem/1.75rem Microsoft YaHei;
        color: #333;
        background-color: #fff;
        text-align: center;
        margin-top: .125rem;
    }
    #foot_guide {
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 2rem;
        display: flex;
        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    .guide_item {
        flex: 1;
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
    }
    .guide_item span{
        font-size: .45rem;
        color: #666;
        margin-top: .2rem;
        text-align: center;
        margin-left: .2rem;
    }
    .guide_item i {
        width: .8rem;
        height: .8rem;
        margin-top: .3rem;
        fill: #ccc;
    }
    .guide_item:nth-of-type(2) i {
        color: #3190e8;
    }
</style>